////
/// Iframe
/// @group patterns
////

.wrapper--iframe {
    height: 0;
    padding-bottom: 56.25%;
    position: relative;
    width: 100%;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        border: 0;
        margin: 0;
        padding: 0;
    }
}
